:root {
  /* Color palette */
  --color-primary: #3b82f6; /* Vibrant blue */
  --color-primary-dark: #2563eb;
  --color-secondary: #10b981; /* Teal/green accent */
  --color-dark-bg-1: #0a0a0a;
  --color-dark-bg-2: #1f1f1f;
  --color-dark-bg-3: #2d2d2d;
  --color-text-primary: #f3f4f6;
  --color-text-secondary: #d1d5db;

  /* Base colors */
  --background: #ffffff;
  --foreground: #171717;
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--color-dark-bg-1);
    --foreground: var(--color-text-primary);
  }
}

/* Custom classes */
.gradient-bg-main {
  background: linear-gradient(to bottom, #000000, #0f172a);
}

.gradient-bg-about {
  background: linear-gradient(to bottom, #0f172a, #1e293b);
}

.gradient-bg-projects {
  background: linear-gradient(to bottom, #1e293b, #000000);
}

.card-hover {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card-hover:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.3);
}

.text-glow {
  text-shadow: 0 0 15px rgba(59, 130, 246, 0.5);
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--color-dark-bg-2);
}

::-webkit-scrollbar-thumb {
  background: var(--color-dark-bg-3);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--color-primary-dark);
}

body {
  color: var(--foreground);
  background: var(--background);
  font-family: var(--font-geist-sans), Arial, sans-serif;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

